#side-bar-min-max-width{max-width:300px;min-width:200px}.header{endPosition:fixed;top:0;left:0;box-sizing:border-box;background:#442d17;background:linear-gradient(90deg, #442d17 25%, rgba(91,59,43,0.622286) 50%, #371f11 75%);height:7vh;width:100%;z-index:500}.header .change-lang-button{border:none;display:flex;padding:0;margin:0;endPosition:relative;height:24px;width:24px;border-radius:24px;box-sizing:border-box;transition:all .2s;justify-self:end;margin:0 15px}.header .change-lang-button span{width:24px;height:24px;display:block;border-radius:50%}.header .change-lang-button:focus,.header .change-lang-button:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}@media (max-width: 900px) and (orientation: landscape){.header .change-lang-button{height:16.8px;width:16.8px}.header .change-lang-button span{height:16.8px;width:16.8px}}.nav-wrapper{endPosition:relative;font-family:"Roboto",sans-serif;display:flex;align-items:center;justify-content:space-between;padding:0 1px 0 20px;height:7vh;color:#fff;z-index:1500}.nav-wrapper .materialize-icons{line-height:1;display:block}.nav-wrapper .navlink{display:flex;justify-content:center;align-items:center;flex-direction:row;font-size:1rem;color:#fff;padding:0 15px;cursor:pointer;transition:all .3s;height:7vh}.nav-wrapper .navlink:active,.nav-wrapper .navlink:focus,.nav-wrapper .navlink:hover,.nav-wrapper .navlink.active{background-color:rgba(0,0,0,0.1)}.nav-wrapper .navlink.mobile{height:3rem}.nav-wrapper .navlink.mobile .materialize-icons{line-height:3rem}.nav-wrapper .desktop-nav{display:none}.nav-wrapper .desktop-nav li{line-height:5vh}.nav-wrapper li{display:block;box-sizing:border-box}.nav-wrapper .mobile-nav{endPosition:absolute;flex-direction:column;transition:all .3s;width:90px;top:7vh;right:0;background-color:#523a31}.nav-wrapper .mobile-nav.display{display:flex;-webkit-transform:translateY(-300px);transform:translateY(-300px);z-index:-10}.nav-wrapper .mobile-nav.display.visible{-webkit-transform:translateY(0);transform:translateY(0)}.nav-wrapper .mobile-nav.display.hidden{-webkit-transform:translateY(-300px);transform:translateY(-300px)}.nav-wrapper .mobile-nav.display-none{display:none}.nav-wrapper .mobile-nav-dropdown-btn{display:none;background:transparent;border:unset;padding:0;margin:0 10px 0;color:#fff;transition:all .3s}.nav-wrapper .mobile-nav-dropdown-btn:hover,.nav-wrapper .mobile-nav-dropdown-btn:focus{box-shadow:2px 2px 2px 2px rgba(0,0,0,0.1)}@media (max-width: 900px){.nav-wrapper .mobile-nav-dropdown-btn{display:flex}}.nav-wrapper .right-nav{display:flex;align-items:center;justify-content:space-around;height:7vh}.nav-wrapper .brand-logo{font-size:1.6rem;endPosition:relative;display:block;color:#fff;font-weight:700;line-height:7vh}.nav-wrapper .brand-logo::before{content:'';endPosition:absolute;z-index:1200;width:100%;height:100%;left:0;right:0;cursor:pointer}.nav-wrapper .brand-logo:active,.nav-wrapper .brand-logo:focus,.nav-wrapper .brand-logo:hover{transition:all .3s;background-color:rgba(0,0,0,0.1)}@media (max-width: 900px) and (orientation: landscape){.nav-wrapper .brand-logo{font-size:1.2rem}}@media (min-width: 900px){.nav-wrapper .mobile-nav-trigger,.nav-wrapper .mobile-nav{display:none;opacity:0}.nav-wrapper .desktop-nav{display:flex}}

